  border-radius: 6px;
  height: 90px;

  margin-top: auto;
  margin-bottom: auto;

  padding: 12px 16px;
}

.transaction-result .heading {
  opacity: 0.9;
  font-family: 'Raleway', sans-serif;
  font-size: 14px;
  text-align: left;
  color: #5B5B5C;
  letter-spacing: 1.02px;
  margin: 0 0 12px;
}

.transaction-result .icon {
  display: inline-block;
  margin-right: 12px;
  height: 18px;
  vertical-align: middle;
}

.transaction-result .icon ~ span {
  vertical-align: middle;
}

.transaction-result .content {
  -ms-flex-align: center;
      align-items: center;
  text-align: left;
}

.transaction-result .value {
  white-space: nowrap;
}

.transaction-result .label, .transaction-result .value {
  font-size: 11px;
}

.metamask--big {
  background-size: contain;
  background: url(data:image/svg+xml;base64,) no-repeat center;
}

.metamask {
  background-size: contain;
  background: url(data:image/svg+xml;base64,) no-repeat center;
}

.parity {
  background-size: contain;
  background: url(data:image/svg+xml;base64,) no-repeat center;
}

.mist {
  background-size: contain;
  background: url(data:image/svg+xml;base64,) no-repeat center;
}


